  padding: 16px;
  font-family: Roboto, sans-serif;
  transition: all .3s;
}

.card__photo {
  width: 160px;
  height: 134px;
  margin: 0 auto;
  display: block;
}

.card__title {
  color: #060b35;
  margin-top: 40px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  margin-top: 4px;
  font-size: 10px;
  line-height: 14px;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover .card__title {
  color: #34568b;
}

.card__description {
  padding-top: 16px;
}

.review {
  justify-content: space-between;
  align-items: center;
  margin-top: 16px;
